@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        background: rgb(180,58,155);
        background: linear-gradient(270deg, rgba(180,58,155,1) 0%, rgba(149,63,162,0.48783263305322133) 56%, rgba(210,69,252,1) 100%);      
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    video {
         display: block;
        @include m.desktop {
            width: 100%;
            
        }
    }

}

.contenido-video{
    z-index: 10; // al estar superpuesto, para que apareza por encima del video
    h2 {
        color:v.$blanco;
        font-size: 7rem;
        margin:0;

    }
    p {
        background-color: v.$rosa;
        color: v.$blanco;
        padding: 2rem 1rem;
        display: inline-block;
        font-weight: 700;
        font-size: 2.4rem;
        margin:0;  
        

    }
}
