@use 'base/variables' as v;
@use 'base/mixins' as m;

.entrada {
    padding: 5rem 0;
    h3 {
        color:v.$blanco;
        text-align: center;
    }

    .entradas-contenido {
        @include m.tablet {
            @include m.grid(2,2rem)
            
        }
        
    }    
}
// & hace referecia al padre
.pase {

    &-dia {
        color: v.$blanco;
        text-align: center;
        font-size: 2.8rem;
        
    }

    &-header {
        background-color: v.$rosa;
        p {
            text-transform: uppercase;
        }

    }
    &-body {
        background-color: v.$blanco;
        ul {
            @include m.resetear-lista;
            li,
            :nth-child(odd) {
                background-color: v.$blanco;
                padding: 0.8rem;
                border-bottom: 0.2rem solid #e1e1e1;
            }
        }

    }
    &-footer {
        background-color: v.$morado;
        font-weight: 700;
        p {
            font-size: 2.6rem;
        }

    }

    &-header,
    &-footer {
        padding: 0.5rem;
        p {
            color:v.$blanco;
            text-align: center;
            margin: 0;
        }
    }
}
    
    
    
