@use 'base/variables' as v;
@use 'base/mixins' as m;


.header {
    background-color: v.$verde;
    padding: 2rem;
    
    @include m.tablet {
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .3rem .3rem .3rem #1111;
        }
        
    }
    
    
    h1{
        color:v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;
            
        }
    }
    .contenido-header {
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            
        }
        
    }
}

.navegacion-principal {
    @include m.tablet { 
        display: flex;
        gap: 1.8rem;
        align-items: center;
    }
    a{
       @include m.tablet {  
            margin: 0;
       }
        display: block;
        text-align: center;
        margin-bottom: 1.5rem;
        color: v.$blanco;
        font-size: 2rem;
        &:hover {        // se pone el & delante del hover para quitar el " " que se crea en css cuando compila
            color:v.$amarillo;
        }
    }

    
    }
