// se puede poner condicionales siempre es falso, pero si queremos que se aplique al especial en una clase concreta, 
// en la clase se pone true y se aplica, siempre con el @if


// se crean mq para telefono, tablet y sobremesa
@mixin telefono {
    @media(min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media(min-width:v.$tablet){
        @content;
    }
}

@mixin desktop {
    @media(min-width:v.$desktop) {
        @content;
    }
        
    }
    @mixin desktopXL {
        @media(min-width:v.$desltopXL) {
            @content;
        }
            
        }

@mixin grid($columnas,$espacio){  // para crear una variable para poder elegir la cantidad de columnas
        display: grid;
        grid-template-columns: repeat($columnas, 1fr);
        gap: $espacio;
}  

@mixin resetear-lista { // ya que las listas tienen padin y marin dados por defoul, con esto lo resetemaos y quitamos los indix
    margin: 0;
    padding: 0;
    list-style: none;
}