@use 'base/variables' as v;
@use 'base/mixins' as m;


.lineup {
   background-color: v.$rosa;
   padding: 5rem 0;
    h3,p {
        text-align: center;
        color:v.$blanco;

    }
    .dia {
        text-transform: uppercase;
        margin: 5rem 0;
    }
} 
.contenido-escenario {
    @include m.tablet {
        @include m.grid(2,5rem);
        
    }
    p {
        color: v.$negro;
    }
    
}
.escenario {
    padding-top: 2rem;

    .nombre-escenario {  
        &:after {    // con este after, generamos un hueco para el icono, es un cuadrado 20*10 centrado
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;


        }
    }

}
.rock .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);

}
.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}
.calendario {
    @include m.resetear-lista;  //importamos el mixin para dejar la lista de "fabrica", sin margin ni padin
    li {
        font-size: 1.8rem;
        padding: 2.5rem;
        font-weight: 700;
            span {
                font-weight: 400;

            }

    }

}



.bg-amarillo {
    background-color: v.$amarillo;
    li:nth-child(odd) { // para coger de la lista los pares o impares, vamos que se salte uno
        background-color: darken(v.$amarillo, 15%);  // darken para oscurecer el color y el porcetaje


    }
}

.bg-verde {
    background-color: v.$verde;
    li:nth-child(odd) {
        background-color: darken(v.$verde, 15%);
    }
}