@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenido-festival {
    padding: 5rem;
    
    @include m.tablet {
        @include m.grid(2,5rem);  // para poner el numero de columnas que queremos
    }
    

}


.informacion-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    
    @include m.tablet {
        margin-top: 0;
        
    }
    h2{
        margin-bottom: 2rem;

    }
    .fecha {
        color: v.$rosa;
        font-weight: 700;
        

    }
}

