@use 'base/variables' as v;
@use 'base/mixins' as m;

// DAR ESTILO A LA GALERIA

.galeria {
    padding: 5rem 0;
    h3 {
        text-align: center;
    }

}

.imagines-galeria {
    @include m.resetear-lista;
    @include m.grid(2, 2rem);

    @include m.telefono {
        @include m.grid(3, 2rem);
        
    }
    @include m.tablet {
        @include m.grid(4, 2rem);
        
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
        
    }
}

// ESTILOS PARA EL OVERLAY CREADO EN JS
.overlay{
    position: fixed; //posicion fija
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center; // centra la imagen

    background-color: rgb(0 0 0 /.5);  // para oscurecer el fondo
 
    picture {
        display: block;
        max-width: 80rem; // para darle un maximo de ancho tiene que se en block
    }
    .btn-cerrar {
        width: 5rem; // creamos el circulo blanco donde ira nuestra X
        height: 5rem;
        background-color: v.$blanco;
        border-radius: 50%;
        display: grid;
        place-items: center;
        color: v.$negro;    // damos forma a nuestra x
        font-weight: 700;
        font-size: 2rem;

        &:hover {     // y que salga la manita cuando pasemos el raton
            cursor: pointer;
            
        }
       
    }
    

}   



